





















import StrongVue from "@/store";

import { Component, Prop } from "vue-property-decorator";

import { addIcons } from "ionicons";
import { wallet, link, checkmark } from "ionicons/icons";

import { WalletData } from "../types";

import TicketStatusComponent from "./TicketStatusComponent.vue";

addIcons({
  "ios-wallet": wallet.ios,
  "md-wallet": wallet.md,

  "ios-link": link.ios,
  "md-link": link.md,

  "ios-checkmark": checkmark.ios,
  "md-checkmark": checkmark.md,
});

@Component({
  name: "WalletComponent",
  components: {
    TicketStatusComponent
  }
})
export default class WalletComponent extends StrongVue {
  @Prop() private walletData!: WalletData;

  private multiColumnOptions = [
      [
        '1111SauE...Sf2w',
        '1111FcDa...A3x1',
      ],
      [
        'Anonymous',
        'TheoXD',
      ]
    ]

  constructor() {
    super();

  }

    getColumnOptions(columnIndex: number, numOptions: number, columnOptions: Array<Array<string>>) {
      const options = [];
      for (let i = 0; i < numOptions; i++) {
        options.push({
          text: columnOptions[columnIndex][i % numOptions],
          value: i
        })
      }

      return options;
    }

  getColumns(numColumns: number, numOptions: number, columnOptions: Array<Array<string>>) {
      const columns = [];
      for (let i = 0; i < numColumns; i++) {
        columns.push({
          name: `col-${i}`,
          options: this.getColumnOptions(i, numOptions, columnOptions)
        });
      }

      return columns;
    }

    async openPicker(numColumns = 1, numOptions = 5, columnOptions = this.multiColumnOptions){
      //const pickerController = document.querySelector("ion-picker-controller");
      //console.info("openPicker");
      //console.info(pickerController);
      //if (!pickerController) return;

      await this.pickerController.componentOnReady();

      const picker = await this.pickerController.create({
        columns: this.getColumns(numColumns, numOptions, columnOptions),
        buttons: [
          {
            text: 'Cancel',
            role: 'cancel'
          },
          {
            text: 'Confirm',
            handler: (value) => {
              console.log(`Got Value ${value}`);
            }
          }
        ]
      });

      await picker.present();
    }
}
