import Vue from "vue";
import Home from "@/views/Home.vue";
import { IonicVueRouter } from "@ionic/vue";

Vue.use(IonicVueRouter);

export default new IonicVueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "root",
      redirect: "/home"
    },
    {
      path: "/home",
      name: "home",
      component: Home
    },
    {
      path: "/polls",
      name: "polls",
      props: {},
      children: [
        {
          path: "/polls/:repo",
          props: true,
          meta: {
            showRepo: true
          },
          children: [
            {
              path: "/polls/:repo/:issue",
              props: true,
              meta: {
                showRepo: true,
                showModal: true
              },
              component: () => import("@/views/Polls.vue")
            }
          ],
          component: () => import("@/views/Polls.vue")
        }
      ],
      component: () => import("@/views/Polls.vue")
    },
    {
      path: "/notifications",
      name: "notifications",
      component: () => import("@/views/Notifications.vue")
    },
    {
      path: '*',
      name: "home",
      component: Home
    }
  ]
});
