import StrongVue from "@/store";
import App from "./App.vue";
import router from "./router";
//import store from "./store";

import Ionic from "@ionic/vue";
import "@ionic/core/css/ionic.bundle.css";

import { ApolloClient } from "apollo-client";
import { createHttpLink } from "apollo-link-http";
import { InMemoryCache } from "apollo-cache-inmemory";

import { provide } from "@vue/composition-api";
import { DefaultApolloClient } from "@vue/apollo-composable";

//import { print } from 'graphql/language/printer';
import mySchema from "raw-loader!@/graphql/schema.graphql";

import VueCompositionApi from "@vue/composition-api";
import VueApexCharts from "vue-apexcharts";
import Clipboard from "v-clipboard";

const typeDefs = mySchema;
const token =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6MywiaWF0IjoxNTkyNjg3OTI2LCJleHAiOjE1OTUyNzk5MjZ9.zm80iFcOAeT8nwu4vIhnDNfHCOQrOOsBw54uFJugd-A";

// HTTP connection to the API
const httpLink = createHttpLink({
  headers: {
    authorization: token ? `Bearer ${token}` : ""
  },
  // You should use an absolute URL here
  uri: "http://localhost:1337/graphql"
});

// Cache implementation
const cache = new InMemoryCache();

// Create the apollo client
const apolloClient = new ApolloClient({
  link: httpLink,
  cache,
  typeDefs,
  resolvers: {}
});

cache.writeData({
  data: {
    members: []
  }
});


StrongVue.use(VueCompositionApi);
StrongVue.use(Ionic);
StrongVue.use(VueApexCharts);
StrongVue.use(Clipboard);
StrongVue.component("apexchart", VueApexCharts);

StrongVue.config.productionTip = false;

new StrongVue({
  router,
  setup() {
    provide(DefaultApolloClient, {
      default: apolloClient
    });
    return {};
  },
  render: h => h(App)
}).$mount("#app");

StrongVue.config.ignoredElements = [/^ion-/];
StrongVue.config.ignoredElements;
