//Solution from https://dev.to/daroco/strongly-typed-vuex-store-in-four-lines-of-typescript-42n6

import Vue, { VueConstructor } from "vue";
import { Store } from "vuex";
import { RootState } from "../store/types";

//Vue.prototype.pickerController = window.pickerController;

abstract class StrongVueClass extends Vue {
  public $store!: Store<RootState>;
  public pickerController: HTMLIonPickerControllerElement;

  constructor() {
    super();

    this.pickerController = window.pickerController;
  }
}
const StrongVue = Vue as VueConstructor<StrongVueClass>;

export default StrongVue;
